.student-col {
	font-weight: 500;
	font-size: 20px;
	line-height: 1;
}

.student-table {
	margin-top: 10px;
	margin-bottom: 20px;
	width: 100%;
	border-collapse: collapse;
	border-spacing: 0;
	border: 1px solid #dfe2e5;
	box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
	border-radius: 5px;
	background-color: #fff;
	color: #333;
	font-size: 14px;
	line-height: 1.42857143;
	vertical-align: middle;
}
