.admin-nav-bar {
	height: 72px;
	background-color: #225ab4;
	width: 100%;
	position: fixed;
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.admin-nav-bar h1 {
	color: #fff;
	margin-left: 20px;
}
.admin-nav-bar .nav-bar-menu {
	margin-right: 30px;
	display: flex;
	justify-content: space-around;
	align-items: center;
	gap: 30px;
	color: white;
	font-size: 20px;
}
.admin-nav-bar .nav-bar-menu .welcome {
	display: flex;
	align-items: center;
	gap: 10px;
	cursor: pointer;
}
.admin-nav-bar .nav-bar-menu .chnage-password {
	display: flex;
	align-items: center;
	gap: 10px;
	cursor: pointer;
}
.admin-nav-bar .nav-bar-menu .chnage-password:hover {
	color: black;
	text-decoration: underline;
}
.admin-nav-bar .nav-bar-menu .logout {
	display: flex;
	align-items: center;
	gap: 10px;
	cursor: pointer;
}
.admin-nav-bar .nav-bar-menu .logout:hover {
	color: red;
	font-weight: bold;
}

.admin-sidebar {
	background-color: #fff;
	border-right: 1px solid #e6e6e6;
	height: calc(100vh - 72px);
	position: fixed;
	top: 0;
	left: 0;
	width: 150px;
	position: fixed;
	z-index: 1;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
	margin-top: 72px;
}
.admin-sidebar .menu-item ul {
	list-style: none;
	padding: 0;
	margin: 0;
}
.admin-sidebar .menu-item ul li a {
	display: flex;
	align-items: center;
	padding: 20px;
	text-decoration: none;
	color: #333;
	transition: background-color 0.3s;
	font-weight: bold;
	font-size: 20px;
}
.admin-sidebar .menu-item ul li a:hover {
	background-color: #f5f5f5;
}
.admin-sidebar .menu-item ul li a svg {
	margin-right: 10px;
}
.admin-sidebar .menu-item ul li a span {
	font-size: 16px;
}

.active {
	background-color: #6983ae;
	color: black;
}
