.container{display:flex;height:calc(100vh - 65px);overflow:hidden;background-color:#d5e7f0}.container .left{flex:0 0 35%;padding:20px 0 20px 20px;border-radius:10px;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:18px;color:#333;gap:10px}.container .left .left__header{display:flex;height:50%;width:100%;gap:20px;background-color:#fff;border-radius:10px}.container .left .left__header .left_header__section{background-color:#df8d29;width:10px;height:100%}.container .left .left__header .right_header_section{flex-grow:1}.container .left .left__buttom{height:50%;display:flex;width:100%;gap:20px;background-color:#fff;border-radius:10px}.container .left .left__buttom .left_buttom__section{background-color:#4306b3;width:10px;height:100%}.container .left .left__buttom .right_buttom_section{flex-grow:1}.container .center{flex:0 0 35%;padding:20px 0 20px 20px;border-radius:10px;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:18px;color:#333;gap:20px}.container .center .center_header{display:flex;height:50%;width:100%;gap:10px;background-color:#fff;border-radius:10px}.container .center .center_header .center_header_left__section{background-color:#ca168e;width:10px;height:100%}.container .center .center_header .center_header_right_section{flex-grow:1}.container .center .center__buttom{height:50%;display:flex;width:100%;gap:20px;background-color:#fff;border-radius:10px}.container .center .center__buttom .center_buttom_left__section{background-color:#1ad43c;width:10px;height:100%}.container .right{flex:0 0 20%;background-color:#fff;padding:20px;border-radius:10px;box-shadow:0 2px 5px rgba(0,0,0,.1);font-size:18px;color:#333;margin:20px}@media(max-width: 768px){.container{flex-direction:column}.container .left,.container .center,.container .right{flex:0 0 100%;margin-bottom:20px}}