.schedules-container{display:flex;flex-direction:column;align-items:center;justify-content:flex-start;height:100vh;width:100%;max-width:100%}.schedules-header{display:flex;justify-content:space-between;align-items:center;width:100%;position:sticky;top:0;z-index:1;margin-bottom:10px}.schedules-header h1{font-size:18px;font-weight:bold;color:#6a0dad}.schedules-header .schedules-right{display:flex;gap:10px}.schedules-header .schedules-right .schedules-icon{font-size:14px;color:#6a0dad;font-weight:bold}.schedules-header .schedules-right span{font-size:14px;color:#6a0dad;font-weight:bold}.schedule-list{list-style-type:none;padding:0;margin:0;width:100%;flex-grow:1;max-height:calc(100vh - 200px);overflow-y:auto;scroll-behavior:smooth}.schedule-list::-webkit-scrollbar{width:8px}.schedule-list::-webkit-scrollbar-thumb{background-color:#6a0dad;border-radius:10px}.schedule-list::-webkit-scrollbar-track{background-color:#f9f9f9}.schedule-item{background-color:#e3f2fd;padding:15px;margin-bottom:10px;border-radius:8px;height:80px;display:flex;align-items:center;gap:15px;transition:background-color .3s ease,box-shadow .3s ease;box-shadow:0 1px 3px rgba(0,0,0,.1)}.schedule-item:hover{background-color:#bbdefb;box-shadow:0 4px 6px rgba(0,0,0,.2)}.schedule-item .schedule-icon{font-size:24px;color:#26a69a}.schedule-item .schedule-content{display:flex;flex-direction:column;justify-content:center;align-items:flex-start;gap:5px}.schedule-item .schedule-icon-list{color:#e737ca;font-weight:bold;font-size:30}.schedule-item .schedule-title{font-size:18px;color:#333;font-weight:600;flex-grow:1}.schedule-item .schedule-time{font-size:16px;color:#666}.schedule-divider{height:1px;background-color:#666;margin:5px 0;width:100%}.no-events-message{font-size:24px;font-weight:600;color:#c65973;margin-top:20px}