/* style.css */
.headerStyle {
  font-weight: bold;
  text-align: center;
  font-size: 18px;
  display: flex;
  justify-content: space-evenly;
}

.score-edit-btn {
  float: right;
  background-color: white;
  color: black;
  border: solid 1px black;
  outline: none;
  padding: 5px;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
  font-size: 15px;
  transition: all 0.3s ease;
}
.score-edit-btn:hover {
  background-color: black;
  color: white;
}