.password-security {
  display: flex;
  overflow: hidden;
}
.password-security .password-chnage {
  min-height: 450px;
  width: 47%;
  margin: 10px;
  padding: 10px;
  box-shadow: 0px 0px 1px 0px rgba(83, 81, 81, 0.75);
  background-color: white;
  border-radius: 5px;
  margin-top: 10px;
}
.password-security .other-security {
  min-height: 500px;
  width: 47%;
  margin: 10px;
  padding: 10px;
  box-shadow: 0px 0px 1px 0px rgba(83, 81, 81, 0.75);
  background-color: white;
  border-radius: 5px;
  margin-top: 20px;
}
.password-security .other-security .two-factor-auth {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 0px 0px 1px 0px rgba(83, 81, 81, 0.75);
  background-color: white;
  margin-top: 20px;
  gap: 20px;
}